.popup {
  width: 100%;
  height: 100vh;

  background-color: rgb(0, 0, 0, .3);
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 5;

  padding: 0;
  margin: 0;
}

.popup__container {
  background-color: #fff;
  position: relative;

  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;

  box-sizing: border-box;
  width: 100%;
  height: 100vh;
  max-width: 520px;
  min-width: 320px;

  padding: 0;
  margin: 0;
  margin-left: auto;
}

.popup__btn_close {
  background-image: url(../../images/delete-icon.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;

  width: 22px;
  height: 22px;

  position: absolute;
  top: 23px;
  right: 23px;
}

.popup__link {
  color: #000;
  font-size: 18px;
  font-weight: 500;
  line-height: 22px;
  text-align: center;
  text-decoration: none;

  display: block;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  transition: .7s ease-in-out;
}

.popup__link:hover {
  cursor: pointer;
  opacity: .5;
}

.popup__link_active {
  border-bottom: 2px solid #000;
  padding-bottom: 4px;
}

.popup__link_film:first-of-type {
  margin-top: auto;
}

.popup__link_film + .popup__link_film {
  margin-top: 28px;
}

.popup__link_profile {
  background-color: #f9f9f9;
  border-radius: 20px;

  font-weight: 500;
  font-size: 14px;
  line-height: 32px;

  width: 100px;
  height: 32px;
  margin-top: auto;
  margin-bottom: 90px;
}

@media screen and (max-width: 400px) {
  .popup__btn_close {
    top: 14px;
    right: 14px;
  }

  .popup__link_active {
    padding-bottom: 7px;
  }

  .popup__link_film + .popup__link_film {
    margin-top: 24px;
  }

  .popup__link_profile {
    margin-bottom: 46px;
  }

}