.SectionHeader {
  color: #000000;
  font-size: 22px;
  font-weight: normal;
  line-height: 27px;
  letter-spacing: -0.04em;
  text-align: left;

  border-bottom: 1px solid #000;

  box-sizing: border-box;
  width: 100%;
  padding: 0;
  padding-bottom: 23px;
  margin: 0;
}

@media screen and (max-width: 400px) {
  .SectionHeader {
    font-size: 18px;
    line-height: 22px;
    padding-bottom: 28px;
  }
}
