.search {
  background-color: #fff;
  border-bottom: 1px solid #e8e8e8;

  box-sizing: border-box;
  width: calc(100% - 140px);

  padding: 70px 0px 69px 0px;
  margin: 0px 70px 10px 70px;
}

.search__box {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.search__form {
  border-radius: 9px;
  overflow: hidden;

  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;

  width: 100%;
  min-height: 72px;
}

.search__input-box {
  background-color: #f9f9f9;
  border: none;
  height: 72px;
  flex-grow: 2;

  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
}

.search__input_error {
  color: #EE3465;
  font-size: 12px;
  font-weight: normal;
  line-height: 12px;
  text-align: center;
  width: 100%;
  padding-top: 18px;
}

.search__input {
  background-color: transparent;
  background-image: url(../../images/search-icon.svg);
  background-repeat: no-repeat;
  background-position: 19px 50%;
  outline: none;
  border: none;

  color: #000;
  font-size: 18px;
  font-weight: normal;
  line-height: 20px;

  height: 100%;
  padding: 25px 0px;
  padding-right: 10px;
  padding-left: 63px;
}

.search__input::placeholder {
  color: #a0a0a0;
  font-size: 18px;
  font-weight: normal;
  line-height: 20px;
}

.search__button {
  background-color: #FF6838;
  background-image: url(../../images/search-btn-icon.svg);
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 48px;
  width: 34px;
  height: 34px;
  margin-right: 30px;
  margin-left: 10px;
}

.search__button:hover {
  opacity: .7;
}

.search__switch-box {
  background-color: #f9f9f9;
  border: none;
  height: 72px;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}

.search__line {
  background-color: #e8e8e8;
  width: 1px;
  height: 40px;
  margin-right: 29px;
}

.search__switch-name {
  color: #000;
  font-size: 13px;
  font-weight: normal;
  line-height: 18px;
  margin-left: 14px;
  margin-right: 30px;
}

@media screen and (max-width: 770px) {
  .search {
    width: calc(100% - 60px);
    padding: 80px 0px 109px 0px;
    margin: 0px 30px 10px 30px;
  }

  .search__line {
    margin-right: 19px;
  }

  .search__switch-name {
    margin-left: 15px;
    margin-right: 20px;
  }

  .search__button {
    margin-right: 19px;
  }
}

@media screen and (max-width: 650px) {
  .search__form {
    flex-flow: column nowrap;
  }

  .search__input-box {
    border-radius: 9px;
    width: 100%;
    margin-bottom: 45px;
  }

  .search__switch-box {
    background-color: transparent;
    height: 20px;
    /* margin-top: 45px; */
  }

  .search__line {
    margin-right: 0px;
    display: none;
  }
}

@media screen and (max-width: 500px) {
  .search__input {
    background-image: none;
    padding: 25px 0px;
    padding-left: 19px;
  }
}

@media screen and (max-width: 400px) {
  .search {
    width: calc(100% - 20px);
    padding: 80px 0px 44px 0px;
    margin: 0px 10px 10px 10px;
  }

  .search__input {
    font-size: 14px;
  }

  .search__input::placeholder {
    font-size: 14px;
  }

  .search__switch-name {
    font-size: 11px;
    margin-left: 13px;
    margin-right: 0px;
  }
}
